import { g2Factory, getConsoleConfig, Utils, dataAdapter, g2Legend, g2Tooltip, Constants, g2Size, g2Style, g2Label } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsoleRoseChart';
var cfg = {
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        var unionProps = Object.assign({}, props, {
            config: newConfig,
            padding: props.padding || newConfig.padding || 'auto',
        });
        return unionProps;
    },
    // 图表绘制主函数，必选
    drawChart: function (chart, config, data) {
        var adaptData = dataAdapter(data, config);
        chart.source(adaptData);
        chart.coord('polar');
        g2Legend(chart, config, config.legend);
        g2Tooltip(chart, config, config.tooltip, { showTitle: false });
        if (config.axis) {
            chart.axis('x', {
                grid: {
                    align: 'center',
                    hideFirstLine: false,
                    hideLastLine: false,
                },
                label: {
                    offset: 10,
                    autoRotate: true,
                    textStyle: {
                        textAlign: 'center',
                    },
                },
            });
            chart.axis('y', {
                tickLine: null,
                label: null,
                line: null,
            });
        }
        else {
            chart.axis(false);
        }
        var colors = config.colors || Utils.get(this, 'consoleTheme.colors_24') || Constants.COLORS_24;
        var geom = chart
            .interval()
            .position('x*y')
            .color('x', colors);
        g2Size(geom, config, config.size);
        g2Style(geom, config, config.style);
        g2Label(geom, config, config.label);
        chart.render();
    },
};
var ConsoleRoseChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsoleRoseChart;
