import { g2Factory, getConsoleConfig, Utils, propertyAssign, propertyMap, dataAdapter, g2Tooltip, getFinalGeomColors, drawLine, g2Legend, g2Guide } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsoleRadarChart';
var cfg = {
    // 初始化前对props的预处理函数
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        // TODO 处理padding
        return Object.assign({}, props, {
            padding: props.padding || newConfig.padding || 'auto',
            config: newConfig,
        });
    },
    drawChart: function (chart, config, data) {
        var defs = {
            x: propertyAssign(propertyMap.xAxis, {}, config.xAxis),
            type: {
                type: 'cat',
            },
        };
        defs.y = propertyAssign(propertyMap.yAxis, {
            type: 'linear',
            tickCount: 5,
        }, config.yAxis);
        var adaptData = dataAdapter(data, config);
        chart.source(adaptData, defs);
        var radius = config.radius < 0 ? 1 : config.radius;
        chart.coord('polar', {
            radius: Math.min(radius, 1),
        });
        chart.axis('x', config.xAxis);
        chart.axis('y', config.yAxis);
        g2Tooltip(chart, config, config.tooltip, {
            crosshairs: null,
        });
        var colors = getFinalGeomColors(data, this.consoleTheme);
        config.colors = colors;
        drawLine(chart, config);
        g2Legend(chart, config, config.legend);
        g2Guide(chart, config, config.guide);
        chart.render();
    },
};
var ConsoleRadarChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsoleRadarChart;
