import { __assign } from 'tslib';
import { g2Factory, getConsoleConfig, Utils, propertyAssign, propertyMap, rectAutoTickCount, dataAdapter, axisX, getFinalGeomColors, getDataIndexColor, axisY, g2Legend, g2Guide, g2Tooltip, drawLine } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsoleLineChart';
var cfg = {
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        return Object.assign({}, props, {
            padding: props.padding || newConfig.padding || 'auto',
            config: newConfig,
        });
    },
    drawChart: function (chart, config, data) {
        var defs = {
            x: propertyAssign(propertyMap.xAxis, {
                type: 'cat',
                // 折线图X轴的范围默认覆盖全部区域，保证没有空余
                range: [0, 1],
            }, config.xAxis),
            type: {
                type: 'cat',
            },
        };
        if (Array.isArray(config.yAxis)) {
            config.yAxis.forEach(function (axis, yIndex) {
                defs["y" + yIndex] = propertyAssign(propertyMap.yAxis, {
                    type: 'linear',
                    tickCount: 5,
                }, axis);
            });
        }
        else {
            defs.y = propertyAssign(propertyMap.yAxis, {
                type: 'linear',
                tickCount: 5,
            }, config.yAxis);
        }
        rectAutoTickCount(chart, config, defs, false);
        var adaptData = dataAdapter(data, config);
        chart.source(adaptData, defs);
        // 设置X轴
        axisX(chart, config);
        var colors = getFinalGeomColors(data, this.consoleTheme);
        config.colors = colors;
        if (Array.isArray(config.yAxis)) {
            config.yAxis.forEach(function (axis, yIndex) {
                var yAxisConfig = {
                    line: {
                        stroke: getDataIndexColor(colors, data, yIndex),
                    },
                };
                if (yIndex !== 0) {
                    yAxisConfig.grid = null;
                }
                axisY(chart, __assign(__assign({}, config), { yAxis: axis }), "y" + yIndex, config.yAxisColor ? yAxisConfig : null);
            });
        }
        else {
            // 设置单个Y轴
            axisY(chart, config);
        }
        g2Legend(chart, config, config.legend);
        g2Guide(chart, config, config.guide);
        g2Tooltip(chart, config, config.tooltip);
        if (Array.isArray(config.yAxis)) {
            config.yAxis.forEach(function (asix, yIndex) {
                drawLine(chart, config, "y" + yIndex);
            });
        }
        else {
            drawLine(chart, config);
        }
        chart.render();
    },
};
var ConsoleLineChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsoleLineChart;
