import { g2Factory, getConsoleConfig, Utils, getFinalGeomColors, propertyAssign, propertyMap, rectAutoTickCount, dataAdapter, axisX, axisY, g2Tooltip, g2Legend, g2Guide, g2Label, g2Size, g2Style } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsoleDotChart';
var cfg = {
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        if (newConfig.jitter) {
            newConfig.xAxis = Object.assign({}, newConfig.xAxis, { type: 'cat' });
        }
        var unionProps = Object.assign({}, props, {
            config: newConfig,
            padding: props.padding || newConfig.padding || 'auto',
        });
        return unionProps;
    },
    drawChart: function (chart, userConfig, data) {
        var config = userConfig;
        var jitter = config.jitter, geomStyle = config.geomStyle;
        var colors = getFinalGeomColors(data, this.consoleTheme);
        var defs = {
            x: propertyAssign(propertyMap.xAxis, {
                type: 'cat',
            }, config.xAxis),
            type: {
                type: 'cat',
            },
        };
        defs.y = propertyAssign(propertyMap.yAxis, {
            type: 'linear',
            tickCount: 5,
        }, config.yAxis);
        rectAutoTickCount(chart, config, defs, false);
        var adaptData = dataAdapter(data, config);
        chart.source(adaptData, defs);
        // 设置X轴
        var xAxis = {};
        if (config.jitter) {
            xAxis.grid = {
                align: 'center',
                lineStyle: {
                    lineWidth: 1,
                },
            };
        }
        // 扰动点图不能打开垂直网格线
        if (config.grid && !config.jitter) {
            xAxis.grid = {
                lineStyle: {
                    lineWidth: 1,
                },
            };
        }
        axisX(chart, config, xAxis);
        // 设置单个Y轴
        axisY(chart, config);
        g2Tooltip(chart, config, config.tooltip, { crosshair: false });
        g2Legend(chart, config, config.legend, 'type');
        g2Guide(chart, config, config.guide);
        var geom = chart
            .point()
            .color('type', colors)
            .position('x*y')
            .shape('circle')
            .style('x*y*type*extra', geomStyle || {})
            .active(false);
        if (jitter) {
            geom.adjust('jitter');
        }
        g2Label(geom, config, config.label);
        g2Size(geom, config, config.size, 'type', 'type');
        g2Style(geom, config, config.style);
        chart.render();
    },
};
var ConsolePointChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsolePointChart;
