import { g2Factory, getConsoleConfig, Utils, propertyAssign, propertyMap, getFinalGeomColors, dataAdapter, g2Tooltip, g2Guide, drawLine } from '@alicloud/console-shared-utils';

var COMPONENT_NAME = 'ConsoleMinilineChart';
var cfg = {
    getUnifiedProps: function (props) {
        var config = props.config;
        var defaultConfig = getConsoleConfig()[COMPONENT_NAME];
        var newConfig = Utils.merge({}, defaultConfig, config);
        var unionConfig = Object.assign({}, props, {
            config: newConfig,
            padding: props.padding || newConfig.padding || 'auto',
        });
        return unionConfig;
    },
    drawChart: function (chart, userConfig, data) {
        var config = userConfig;
        var defs = {
            x: propertyAssign(propertyMap.xAxis, {
                type: 'cat',
                range: [0, 1],
            }, config.xAxis),
            type: {
                type: 'cat',
            },
        };
        if (Array.isArray(config.yAxis)) {
            config.yAxis.forEach(function (axis, yIndex) {
                defs["y" + yIndex] = propertyAssign(propertyMap.yAxis, {
                    type: 'linear',
                    tickCount: 5,
                }, axis);
            });
        }
        else {
            defs.y = propertyAssign(propertyMap.yAxis, {
                type: 'linear',
                tickCount: 5,
            }, config.yAxis);
        }
        var colors = getFinalGeomColors(data, this.consoleTheme);
        config.colors = colors;
        var adaptData = dataAdapter(data, config);
        chart.source(adaptData, defs);
        chart.axis(false);
        chart.legend(false);
        // tooltip, 迷你图默认关闭tooltip
        if (!config.tooltip) {
            config.tooltip = false;
        }
        g2Tooltip(chart, config, config.tooltip);
        // 绘制辅助线，辅助背景区域
        g2Guide(chart, config, config.guide);
        drawLine(chart, config);
        chart.render();
    },
};
var ConsoleMinilineChart = g2Factory(COMPONENT_NAME, cfg);

export default ConsoleMinilineChart;
